x;\n    min-height: 650px;\n    flex-flow: column;\n    justify-content: center;\n    text-align: center;\n    height: 90vh;\n    background-color: lightblue;\n    color: darkblue;\n    font-size: 18px;\n    &__title {\n        font-size: 3em;\n        line-height: 1.3em;\n        font-weight: bold;\n        &:last-child{\n            font-weight: normal;\n            font-size: 2em;\n        }\n    }\n}\n\n.avatar {\n    width: 330px;\n    height: 330px;\n    margin: 0 auto;\n    box-shadow: 0 2em 4em rgba(0, 0, 0, .1);\n    border-radius: 5px;\n    font-size: 1.2em;\n    display: flex;\n    justify-content: center;\n    flex-flow: column;\n    border-radius: 160px;\n    background-color: darken(lightblue, 20%);\n    margin-bottom: 15px;\n\n    &__shape {\n        width: 300px; //required\n        height: 300px; //required\n        margin: 0 auto;\n        position: relative;\n        // background: orange;\n        -webkit-shape-outside: circle(50% at 50% 50%);\n        shape-outside: circle(50% at 50% 50%);\n        -webkit-clip-path: circle(50% at 50% 50%);\n        clip-path: circle(50% at 50% 50%);\n\n        img {\n            width: 100%;\n            height: 100%;\n            transition: all .2s ease;\n        }\n    }\n\n    &__caption {\n        position: absolute;\n        top: 50%;\n        left: 50%;\n        transform: translate(-50%, 20%);\n        text-transform: uppercase;\n        text-align: center;\n        color: #fff;\n        font-size: 30px;\n        font-weight: 700;\n        opacity: 0;\n        transition: all .2s ease-in;\n        backface-visibility: hidden; // fix for text animation\n    }\n\n    &:hover &__caption {\n        opacity: 1;\n        transform: translate(-50%, -50%);\n    }\n\n    &:hover &__img {\n        transform: scale(1);\n        filter: blur(2px) brightness(80%);\n        // make blue\n        // brightness > 100% -> brighter\n    }\n}",".navigation {\n    display: flex;\n    justify-content: space-between;\n    color: $font-on-primary;\n    background-color: $primary-variant-color;\n    &__checkbox {\n        display: none;\n    }\n    \n    &__button {\n        display: none;\n        @include responsive(phone) {\n            display: block;\n            background-color: #ffffff;\n            height: 70px;\n            width: 70px; \n            position: fixed;\n            top: 50px;\n            right: 50px;\n            border-radius: 50%;\n            z-index: 10;\n            box-shadow: 0 10px 30px rgba(#000, .3);\n            text-align: center;\n            cursor: pointer;\n        }\n    }\n    \n    &__background {\n        display: none;\n        @include responsive(phone) {\n            display: block;\n            height: 60px;\n            width: 60px;\n            border-radius: 50%;\n            position: fixed;\n            top: 55px;\n            right: 55px;\n            background-image: radial-gradient(lighten(blue, 20%), darken(blue, 10%));\n            z-index: 5;\n            transition: all .3s ease-out;        \n        }\n    }\n\n    &__brand {\n        display: flex;\n        font-size: 2em;\n        align-items: center;\n        padding: 0 20px;\n        @include responsive(phone){\n            display: none;\n        }\n    }\n    &__brand &__link:link,\n    &__brand &__link:visited {\n        color: $font-on-primary;\n    }\n    \n    &__nav{\n        display: flex;\n        position: relative;\n        @include responsive(phone){\n            height: 100vh;\n            width: 100%;\n            position: fixed;\n            top: 0;\n            right: 0;\n            z-index: 8;\n            opacity: 0;\n            width: 0;\n            transition: all .3s;\n        }\n    }\n    \n    &__list{\n        display: flex;\n        flex-direction: row;\n        justify-content: flex-end;\n        position: relative;\n        padding: 10px 20px;\n        margin-bottom: 0;\n        @include responsive(phone) {\n            position: absolute;